export class VeicleService {
    veicles = [
        {
            id: '1',
            name: 'Carro',
            model: 'Gol',
            color: 'Prata',
            licensePlate: 'AAS-8872'
        },
        {
            id: '2',
            name: 'Carro',
            model: 'Fusca',
            color: 'Azul',
            licensePlate: 'OIU-6543'
        },
        {
            id: '3',
            name: 'Moto',
            model: 'Honda CB 650',
            color: 'Prata',
            licensePlate: 'AAS-8872'
        }
    ]

    removeVeicle(veicle) {
        let index = this.veicles.indexOf(veicle);
        if (index > -1) {
            this.veicles.splice(index, 1);
        }
    }

    addVeicle (veicle){
        this.veicles.push(veicle);
        }
}


